  constructor() {
    $("input[type='tel']").mask('+7 (999) 999-99-99');
    if (Resp.isDesk) {
      $('body').addClass('with-hover');
      $('.header__top__contacts__button').click(() => {
        this.tl.reverse();
        $('.header').addClass('fix');
      });
    }
    if (!Resp.isDesk) {
      $('.header').addClass('fixed active visible');
      $('.mobile-active').addClass('active');
      $('.products').addClass('active');
    }

    $('.anchor').click(function() {
      $('html, body').animate(
        {
          scrollTop: $(this.getAttribute('href')).offset().top,
        },
        'slow'
      );
    });

    this.tl = new TimelineLite({
      paused: true,
      immediateRender: false,
    });
    this.tl.set('.header', {
      y: 0,
    });
    this.tl
      .to('.header', 0.4, {
        autoAlpha: 0,
        onComplete: () => {
          $('.header').addClass('fixed active visible');
        },
      })
      .to('.header', 0.5, {
        autoAlpha: 1,
        onReverseComplete: () => {
          $('.header').removeClass('fixed active visible');
        },
      });

    this.popUpAnimate = new TimelineLite({
      paused: true,
    });
    this.popUpAnimate.to('.popup', 0.3, {
      autoAlpha: 1,
      onReverseComplete() {
        $('body').removeClass('hide');
        $('header').removeClass('popup-on');
        $('html').removeClass('hide');
      },
    });

    this.popUpSucces = new TimelineLite({
      paused: true,
    });
    this.popUpSucces
      .to('.popup__main', 0.3, {
        autoAlpha: 0,
      })
      .to('.popup__content__send', 0.3, {
        autoAlpha: 1,
      });

    this.scrollTop = 0;
    // initialize after construction
    this.init();
  }

  throttle = (func, wait) => {
    let context, args, timeout, throttling, more, result;
    const whenDone = debounce(() => {
      more = throttling = false;
    }, wait);
    return function() {
      context = this;
      args = arguments;
      var later = function() {
        timeout = null;
        if (more) func.apply(context, args);
        whenDone();
      };
      if (!timeout) timeout = setTimeout(later, wait);
      if (throttling) {
        more = true;
      } else {
        result = func.apply(context, args);
      }
      whenDone();
      throttling = true;
      return result;
    };
  };

  validateForm(container) {
    $('.popup__content__form__label').removeClass('error');
    $('.iferror').hide();
    $('.ifok').hide();
    const pattern = /^([a-z0-9_\.-])+@[a-z0-9-]+\.([a-z]{2,4}\.)?[a-z]{2,4}$/i;
    let errors = 1;
    const site = $(container).find('.site-f');
    const name = $(container).find('.name-f');
    const email = $(container).find('.mail-f');
    const phone = $(container).find('.phone-f');

    console.log(site);
    if (site.length && site.val().length < 2) {
      errors = 0;
      site.parent().addClass('error');
    }

    if (name.length && name.val().length < 2) {
      errors = 0;
      name.parent().addClass('error');
    }

    if (email.length && !pattern.test(email.val())) {
      errors = 0;
      email.parent().addClass('error');
    }

    if (phone.length && phone.val() < 18) {
      errors = 0;
      phone.parent().addClass('error');
    }
    if (errors == 1) {
      // $.ajax({
      //   url: "/local/ajax/send.php",
      //   type: "POST",
      //   dataType: "html",
      //   data: $("#CPAFORM").serialize(),
      //   success: function(response) {
      //     $(".ifok").show();
      //     $("#CPAFORM")[0].reset();
      //     ga("send", "event", "rate_agency", "reg");
      //     this.popUpSucces.play();
      //   },
      //   error: function(response) {}
      // });
      this.popUpSucces.play();
    } else {
      $('.iferror').show();
    }
  }

  popUpActions = () => {
    const popUpTrigger = $('.popup__trigger');
    const popUpContainer = $('.popup');
    const popUpClose = $('.popup__content__close');
    const popUpSubmit = $('.popup__content__form__button');

    popUpTrigger.click(e => {
      e.preventDefault();
      this.popUpAnimate.play();
    });
    popUpClose.click(() => {
      this.popUpAnimate.reverse();
      this.popUpSucces.reverse();
    });

    popUpSubmit.click(e => {
      e.preventDefault();
      const _this = $(e.currentTarget);
      const parentForm = $(_this).parent('.popup__content__form');
      this.validateForm(parentForm);
    });
  };

  mobileActions() {
    const menuOpenBtn = $('.header__top__burger');
    const menuContainer = $('.header__bottom');
    const menuChildOpen = $('.header__menu__link.has-active');

    const toggleMenu = () => {
      menuOpenBtn.toggleClass('active');
      menuContainer.toggleClass('active');
      $('body').toggleClass('hide');
      $('html').toggleClass('hide');
      if (!menuOpenBtn.hasClass('active')) {
        $('.sub__menu').removeClass('active');
        $('.header__menu__link').removeClass('active');
        $('.mobile-active').addClass('active');
        $('.products').addClass('active');
      }
    };

    menuOpenBtn.click(function() {
      toggleMenu();
    });

    menuChildOpen.click(function() {
      if (
        !$(this)
          .siblings('.sub__menu')
          .hasClass('active')
      ) {
        $('.header__menu__link').removeClass('active');
        $('.sub__menu').removeClass('active');
        $(this)
          .siblings('.sub__menu')
          .addClass('active');
        $(this).addClass('active');
      } else {
        $('.sub__menu').removeClass('active');
        $(this).removeClass('active');
      }
    });

    const phoneBtn = $('.header__top__contacts__button');

    phoneBtn.click(function() {
      menuOpenBtn.removeClass('active');
      menuContainer.removeClass('active');
      $('.sub__menu').removeClass('active');
      $('.header__menu__link').removeClass('active');
      $('.mobile-active').addClass('active');
      $('.products').addClass('active');
      $('body').removeClass('hide');
      $('html').removeClass('hide');
      $(this)
        .siblings('.header__top__contacts__numbers')
        .toggleClass('active');
    });

    const $win = $(window); // or $box parent container
    const $box = $('.header__top__contacts__numbers');

    $win.on('click.Bst', function(event) {
      if (
        $box.has(event.target).length == 0 &&
        !$box.is(event.target) &&
        phoneBtn.has(event.target).length == 0 && !phoneBtn.is(event.target)
      ) {
        $box.removeClass('active');
      }
    });
  }
