  faqAccordion() {
    const accordionContent = $('.accordion-container');
    const accordionHeader = $('.accordion-title');
    $(accordionHeader).click(function() {
      if ($(this).hasClass('open')) {
        $(this)
          .next(accordionContent)
          .slideUp(300);
        $(this)
          .next(accordionContent)
          .parent()
          .removeClass('active');
        $(this).removeClass('open');
      } else {
        // close other content
        $(accordionHeader)
          .not(this)
          .next(accordionContent)
          .slideUp('slow');
        $(accordionHeader)
          .not(this)
          .removeClass('open');
        $(accordionContent)
          .not(this)
          .removeClass('active');
        $(this)
          .next(accordionContent)
          .slideDown(300);
        $(this)
          .next(accordionContent)
          .parent()
          .addClass('active');
        $(this).addClass('open');
      }
    });
  }
